<template>
  <div id="app" :class="{'app-mask': showFlag}">
    <vue-particles
      v-if="bgFlag && !mobileFlag"
      class="app-bg"
      :particleOpacity="0.85"
      :particlesNumber="14"
      shapeType="circle"
      :particleSize="4"
      :linesWidth="1"
      :lineLinked="true"
      :lineOpacity="0.55"
      :linesDistance="160"
      :moveSpeed="2"
      :hoverEffect="true"
      hoverMode="grab"
      :clickEffect="true"
      clickMode="push"
    ></vue-particles>
    <router-view />
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  name: 'App',
  data() {
    return {
      showFlag: false,
      bgFlag: false,
      mobileFlag: false
    }
  },
  computed: {
    ...mapGetters([
      'githubUsername',
      'htmlTitle'
    ])
  },
  created() {
    this.$store.dispatch("Init")
    this.$store.dispatch("GetInfo")
    this.$setTitle(this.$route.meta.title)
    let windowSize = this.$util.getWindowSize()
    let pathArr = this.$route.path.split("/")
    if (pathArr[1] == "user" && windowSize.height > windowSize.width * 1.2) {
      this.$router.push("/mobile/user/blog")
    }
    if (pathArr[1] == "mobile" && windowSize.height <= windowSize.width * 1.2) {
      this.$router.push("/")
    }
  },
  mounted() {
    this.getSpecialDate()
    const { body } = document
    const rect = body.getBoundingClientRect()
    if (rect.width < 768) {
      // 手机
      this.mobileFlag = true
    } else {
      this.mobileFlag = false
    }
  },
  methods: {
    /**
     * 获取特定时间
     */
    getSpecialDate() {
      let mouth = new Date().getMonth()
      let day = new Date().getDate()
      let weekday = new Date().getDay()
      if ((mouth === 11 && day === 13) || (mouth === 3 && day === 4)) {
        this.showFlag = true
      } else {
        this.showFlag = false
      }
      if (weekday === 0 || weekday === 6) {
        this.bgFlag = true
      } else {
        this.bgFlag = false
      }
    }
  }
}
</script>

<style>
.app-mask {
  filter: grayscale(100%);
}

.app-bg {
  height: 100%;
  width: 100%;
  position: fixed;
}
</style>


